import {customElement, findElementsInSlot} from '@here/hds-base';
import {ListItem} from '@here/hds-list';
import {html} from 'lit';
import {property, query} from 'lit/decorators.js';
import {classMap} from 'lit/directives/class-map.js';
import {styles} from './hds-vertical-navigation-sub-list-item.css.js';
import {VerticalNavigationSubList} from './hds-vertical-navigation-sub-list.js';
import {VerticalNavigationSelectPayload} from './hds-vertical-navigation.js';

/**
 * @slot default Content for the item's label
 *
 * @event {CustomEvent<{verticalSubListItemId: string}>}  subListItemSelect (**Internal event**) Fired when item has been selected, internal use only `VerticalNavigationSelectPayload`
 */
@customElement('hds-vertical-navigation-sub-list-item')
export class VerticalNavigationSubListItem extends ListItem {
  @property({type: Boolean, attribute: false, reflect: true}) hasSubList = false;
  @query('.sub-list-wrapper') subListElement!: HTMLElement;
  @query('slot:not([name])') protected slotElement!: HTMLSlotElement;

  private _assignedElementsCache: Element[] = [];

  static get styles(): any {
    return [super.styles, styles];
  }

  protected firstUpdated() {
    super.firstUpdated();
    this.hasTooltip = false;
    this._slotChange();
  }

  protected async updated(changedProperties) {
    this._updateAssignedElements();

    if (changedProperties.has('selected')) {
      if (this.selected && !this.disabled) {
        this.dispatchEvent(
          new CustomEvent<VerticalNavigationSelectPayload>('subListItemSelect', {
            detail: {verticalSubListItemId: this.id},
            bubbles: true,
            composed: true,
          })
        );
      }
    }
  }

  private _slotChange() {
    this._assignedElementsCache = findElementsInSlot<VerticalNavigationSubList>(
      this.slotElement,
      VerticalNavigationSubList
    );

    this._assignedElementsCache.forEach(e => {
      if (e instanceof VerticalNavigationSubList) {
        e.slot = 'sub-list';
      }
    });

    this._updateAssignedElements();
  }

  private _updateAssignedElements() {
    this._assignedElementsCache.forEach(e => {
      if (e instanceof VerticalNavigationSubList) {
        e.disabled = this.disabled;
      }
    });
  }

  protected render() {
    const classes = {
      disabled: this.disabled || this.parentDisabled,
    };

    return html`
      <div
        class="-hds-vertical-navigation-sub-list-item ${classMap(classes)}"
        @click="${this._handleClick}"
        @slotchange="${this._slotChange}"
      >
        <div class="main-list-wrapper">${super.render()}</div>
        <div class="sub-list-wrapper">
          <slot name="sub-list"></slot>
        </div>
      </div>
    `;
  }

  private _handleClick(e: Event) {
    e.stopPropagation();
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-vertical-navigation-sub-list-item': VerticalNavigationSubListItem;
  }
}
